<template>
	<div>
		<div class="createtop">
			<img class="createicon" src='../../assets/images/master/create_rules.png'/>
			<div class="creatext"></div>
		</div>
		<div class="crbox">
			<p>1. live</p>
			<p>2.  <span class="crfont1" @click="showExample"></span> </p>
			<p><span class="crfont1"></span></p>
		</div>
		<ul class="ibox crbox2">
			<li>
				<div class="crtitle left"></div>
				<input class="crinp left" type="text" placeholder="" v-model="selectbroker"/>
				<div class="crightimgbox" @click="showcover()">
					<img class="crightimg" src="../../assets/images/master/right_icon2.png" alt="">
				</div>
			</li>
			<li>
				<div class="crtitle left"></div>
				<input class="crinp left" type="text" placeholder="" v-model="selectserverlive"/>
				<div class="crightimgbox" @click="showserver()">
					<img class="crightimg" src="../../assets/images/master/right_icon2.png" alt="">
				</div>
			</li>
			<li>
				<div class="crtitle left"></div>
				<input class="crinp left" type="text" placeholder="" v-model="bordername"/>
			</li>
			<li>
				<div class="crtitle left"></div>
				<input class="crinp left" type="text" placeholder="MT4" v-model="borderid"/>
			</li>
			<li>
				<div class="crtitle left"></div>
				<input class="crinp left" type="text" placeholder="MT4" v-model="borderpwd"/>
			</li>
		</ul>
		<div class="creremark">
			<img class="cretip" src="../../assets/images/master/tips.png" alt="">
			<div class="crewatch">()</div>
		</div>
		<button class="crbtn" @click="submit()">&nbsp;&nbsp;&nbsp;&nbsp;</button>

		<!--  -->
		<div class="bomb" v-if="rdectionbox">
			<div class="bombin">
				<div class="sfixedbox" @click="hidecover()"></div>
				<div class="sfixed upborders">
					<div class="upborders_search">
						<div class="upborders_searchimg2" @click="searchbroker()">
							<img class="upborders_searchimg" src="../../assets/images/master/searchicon.png" alt="">
						</div>
						<div class="upborders_line"></div>
						<input class="upborders_searchinp" type="text" v-model="searchtext" placeholder=""/>
					</div>
					<div class="upbordersrcoll">
						<ul class="upbordersbox">
							<li v-for="(item,index) in allbroker" :key="index" v-bind:class='{livefont:borderindex==index}' @click="bordersele(item,index)">{{item.SHOWNAME}}</li>
						</ul>
					</div>
					<div class="upborders_hand" @click="hidecover()"></div>
				</div>
			</div>
		</div>
		<!--  -->
		<div class="bomb" v-if="serverlive">
			<div class="bombin">
				<div class="sfixedbox" @click="serverclose()"></div>
				<div class="sfixed upborders">
					<div class="upborders_server"></div>
					<div class="upbordersrcoll">
						<ul class="upbordersbox">
							<li v-for="(item,index) in serverlistlives" :key="index" v-bind:class='{livefont:liveindex==index}' @click="chooselive(item,index)">{{item.SERVERNAME}}</li>
						</ul>
					</div>
					<div class="upborders_hand" @click="serverclose()"></div>
				</div>
			</div>
		</div>
		<div class="masterbomb" v-if="phonemsgbox">{{phonemsg}}</div>

		<!--  -->
		<div class="bomb" v-if="Example1">
			<div class="fobombin">
				<div class="fobomb">
					<div class="borderbombtitle"></div>
					<div class="border_bombnei">11002120
						3200301</div>
					<button @click="closeExample()" class="fobombbtn"></button>
				</div>
			</div>
		</div>

		<!--  -->
		<div class="bomb" v-if="tipsuccess">
			<div class="fobombin">
				<div class="fobomb">
					<div class="borderbombtitle"></div>
					<div class="border_bombnei">
						1
					</div>
					<button @click="sucConfirm()" class="fobombbtn"></button>
				</div>
			</div>
		</div>

	</div>
</template>

<style>
	@import '../../assets/css/create.css';
	@import '../../assets/css/follow.css';
</style>

<script>
import http from '@/lib/http';
import {brokers,serverlistlive,serverlistdemo} from '@/lib/brokers';
export default {
	data(){
		return {
			rdectionbox:false,//
			rules:[
				{num:'1-10',profit:'10//'},
				{ num: '11-20', profit: '20//' },
				{ num: '21-50', profit: '30//' },
				{ num: '51-100', profit: '50//' },
				{ num: '101-200', profit: '70//' },
				{ num: '201-300', profit: '100//' },
				{ num: '301-500', profit: '130//' },
				{ num: '500-1000', profit: '150//' },
				{ num: '1000', profit: '180//' },
			],
			headswitch: 'live',
			allbroker: [],	//
			serverlistlives: [],	//live
			selectbroker: '',	//
			selectserverlive: '',	//
			choosetype: '',	//broker serverlive serverdemo
			searchtext: '',	//
			serverlive: false,//
			bordername:'',//
			borderid:'',//
			borderpwd:'',//
			phonemsg:'',//
			phonemsgbox:false,//
			borderindex:-1,//
			liveindex:-1,//
			Example1:false,//
			Example2:false,//
			tipsuccess:false,//
		}
	},
	created(){
		const that = this;
		// console.log(serverlistlive)
		that.allbroker = brokers;
	},
	methods: {
		// 
		searchbroker:function(){
			const that = this;
			that.allbroker = [];
			brokers.forEach(item => {
				if(item.SHOWNAME!=undefined){
					if ((item.SHOWNAME.toLowerCase()).indexOf(that.searchtext.toLowerCase())!=-1){
						that.allbroker.push({
							'SHOWNAME': item.SHOWNAME,
							'ID': item.ID
						})
					}
				}
			});	
		},
		// 
		showcover:function(){
			const that = this;
			that.allbroker = brokers;
			that.rdectionbox = true;
		},
		// 
		hidecover:function(){
			const that = this;
			that.rdectionbox = false;
		},
		// 
		showserver:function(){
			const that = this;
			if(that.selectbroker==''){
				that.phonemsg = '';
				that.phonemsgbox = true;
				setTimeout(() => {
					that.phonemsgbox = false;
				}, 1000);
			}else{
				that.serverlive = true;
			}			
		},
		// 
		serverclose:function(){
			const that = this;
			that.serverlive = false;
		},

		// 
		bordersele:function(item,index){
			const that = this;
			that.selectbroker = item.SHOWNAME;
			that.selectserverlive = '';
			that.borderindex = index;
			that.liveindex = -1;//			
			that.serverlistlives = eval('('+serverlistlive['bk'+item.ID]+')');
			// console.log(that.serverlistlives)
			setTimeout(() => {
				that.rdectionbox = false;
			}, 300);
		},
		// 
		chooselive:function(item,index){
			const that = this;
			that.selectserverlive = item.SERVERNAME;
			that.liveindex = index;
			setTimeout(() => {
				that.serverlive = false;
			}, 300);			
		},
		// 
		showExample(){
			const that = this;
			that.Example1 = true;
		},
		// 
		showExample2(){
			const that = this;
			that.Example2 = true;
		},
		closeExample(){
			const that = this;
			that.Example1 = false;
			that.Example2 = false;
		},
		// 
		sucConfirm:function(){
			const that = this;
			that.tipsuccess = false;
			setTimeout(() => {
				that.$router.go(-1);
			}, 1000);
		},
		// 
		submit:function(){
			const that = this;
			if(that.selectbroker==''){
				that.phonemsg = '';
				that.phonemsgbox = true;
				setTimeout(() => {
					that.phonemsgbox = false;
				}, 1000);
			}else if(that.selectserverlive==''){
				that.phonemsg = '';
				that.phonemsgbox = true;
				setTimeout(() => {
					that.phonemsgbox = false;
				}, 1000);
			}else if(that.bordername==''){
				that.phonemsg = '';
				that.phonemsgbox = true;
				setTimeout(() => {
					that.phonemsgbox = false;
				}, 1000);
			}else if(that.borderid==''){
				that.phonemsg = 'MT4';
				that.phonemsgbox = true;
				setTimeout(() => {
					that.phonemsgbox = false;
				}, 1000);
			}else if(that.borderpwd==''){
				that.phonemsg = 'MT4';
				that.phonemsgbox = true;
				setTimeout(() => {
					that.phonemsgbox = false;
				}, 1000);
			}else{
				var data={
					'business': that.selectbroker,
					'mt4server': that.selectserverlive,
					'sign_name': that.bordername,
					'mt4id': that.borderid,
					'mt4pwd': that.borderpwd,
					'type': 1
				}
				http.post('/api.signal/add_master_signal', {data:JSON.stringify(data)})
				.then(function (res) {
					if(res.code == 200) {
						that.allbroker = [];//
						that.serverlistlives = [];//
						that.selectbroker = '';//
						that.selectserverlive = '';//
						that.bordername = '';//
						that.borderid = '';//
						that.borderpwd = '';//
						that.borderindex = -1;//
						that.liveindex = -1;//
            			that.tipsuccess = true;
					}else{
            			that.phonemsg = res.msg;
						that.phonemsgbox = true;
						setTimeout(() => {
							that.phonemsgbox = false;
						}, 1500);
					}
				})
			}
		}
	}
  
}
</script>


